    background-size: cover;
    background-position: center;
    position: absolute;
    visibility: visible;
    overflow: hidden;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 1;
    -moz-animation: pulse 2s linear infinite;
    -webkit-animation: pulse 2s linear infinite;
    animation: pulse 2s linear infinite;
